<template>
  <v-container fluid>
      <v-layout column align-center>
        <br/><br/>
        <h2>Is Gemma Great?</h2>
        <div>
          <v-btn @click="message ='You are right!'">Yes</v-btn>
          <v-btn @click="message = 'Fool! You could not be more wrong.'">No</v-btn>
        </div>
        <br/><br/><br/>
        <h1>{{ message }}</h1>
      </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      message: ''
    }
  }
}
</script>



